import { useState, useEffect } from 'react';

export function useFavorites(userId) {
    const [favorites, setFavorites] = useState([]);

    // Ly danh sch favorites t localStorage theo userId khi component mount, sau s i thnh API
    useEffect(() => {
        const savedFavorites = JSON.parse(localStorage.getItem(`favorites_${userId}`)) || [];
        setFavorites(savedFavorites);
    }, [userId]);

    // Thm sn phm vo danh sch favorites
    const addToFavorites = (product) => {
        const newFavorites = [...favorites, product];
        setFavorites(newFavorites);
        localStorage.setItem(`favorites_${userId}`, JSON.stringify(newFavorites));
    };

    // Xa sn phm khi danh sch favorites
    const removeFromFavorites = (productId) => {
        const newFavorites = favorites.filter(item => item.id !== productId);
        setFavorites(newFavorites);
        localStorage.setItem(`favorites_${userId}`, JSON.stringify(newFavorites));
    };

    // Kim tra xem sn phm  c trong favorites cha
    const isFavorite = (productId) => {
        return favorites.some(item => item.id === productId);
    };

    // Hm toggle (thm hoc xa) sn phm khi favorites
    const toggleFavorite = (product) => {
        if (isFavorite(product.id)) {
            removeFromFavorites(product.id);
        } else {
            addToFavorites(product);
        }
    };

    return {
        favorites,
        addToFavorites,
        removeFromFavorites,
        isFavorite,
        toggleFavorite, // Thm hm ny  s dng trong cc component
    };
}
