import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useNavigate } from 'react-router-dom';
export default function RegisterBox() {
    const [name, setName] = useState(""); 
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [error, setError] = useState("");
    const navigate = useNavigate();
    const handleSubmit = async (e) => {
        e.preventDefault(); // Ngn chn reload trang

        // Kim tra xem mt khu c khp khng
        if (password !== confirmPassword) {
            setError("Passwords do not match");
            return;
        }

        try {
            const response = await fetch("https://projectky320240926105522.azurewebsites.net/api/Auth/register", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({ name, email, password }), // Gi name thay v username
            });

            if (!response.ok) {
                const errorText = await response.text(); // Nhn thng ip li di dng vn bn
                if (errorText.includes("already exists")) {
                    setError("The account already exists."); // Thng bo khi ti khon  tn ti
                } else {
                    setError(errorText); // Cp nht trng thi li vi thng ip t API
                }
                return;
            }
            
    
           
            navigate("/login"); // iu hng v trang ch sau khi ng nhp thnh cng
            // Nu ng k thnh cng, bn c th iu hng hoc thc hin hnh ng khc
            // V d: window.location.href = "/login";
        } catch (error) {
            console.error("Error:", error);
            setError("An error occurred during registration");
        }
    };

    return (
        <section className="login_box_area section_gap">
            <div className="container">
                <div className="row">
                    <div className="col-lg-6">
                        <div className="login_form_inner">
                            <h3>Create an Account</h3>
                            {error && <p className="error-message">{error}</p>} {/* Hin th thng bo li */}
                            <form className="row login_form" onSubmit={handleSubmit} id="contactForm" noValidate>
                                <div className="col-md-12 form-group">
                                    <input
                                        type="text"
                                        className="form-control"
                                        id="RegName"
                                        name="RegName"
                                        placeholder="Name" // Thay i placeholder thnh "Name"
                                        value={name}
                                        onChange={(e) => setName(e.target.value)} // Cp nht state name
                                        required // Bt buc nhp tn
                                    />
                                </div>
                                <div className="col-md-12 form-group">
                                    <input
                                        type="email"
                                        className="form-control"
                                        id="RegEmail"
                                        name="RegEmail"
                                        placeholder="Email"
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="col-md-12 form-group">
                                    <input
                                        type="password"
                                        className="form-control"
                                        id="RegPassword"
                                        name="RegPassword"
                                        placeholder="Password"
                                        value={password}
                                        onChange={(e) => setPassword(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="col-md-12 form-group">
                                    <input
                                        type="password"
                                        className="form-control"
                                        id="RegConfirmPassword"
                                        name="RegConfirmPassword"
                                        placeholder="Confirm Password"
                                        value={confirmPassword}
                                        onChange={(e) => setConfirmPassword(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="col-md-12 form-group">
                                    <button type="submit" className="primary-btn">Register</button>
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div className="login_box_img">
                            <img className="img-fluid" src="img/login.jpg" alt="" />
                            <div className="hover">
                                <h4>Already have an account? Login here</h4>
                                <p>There are advances being made in science and technology every day, and a good example of this is the</p>
                                <Link className="primary-btn" to="/login">Login to Account</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}
