import { useState, useEffect } from 'react';
import { useFavorites } from './useFavorites';

export function useCheckUserLogin() {
    const [userId, setUserId] = useState(null);
    const { toggleFavorite, isFavorite } = useFavorites(userId); // S dng useFavorites

    useEffect(() => {
        const storedUserId = localStorage.getItem('currentUser'); // Ly userId t localStorage
        if (storedUserId) {
            setUserId(storedUserId); // Nu c userId trong localStorage, set userId
        } else {
            const checkUserLogin = async () => {
                try {
                    const response = await fetch('https://yourapi.com/current-user');
                    const data = await response.json();
                    if (data.userId) {
                        setUserId(data.userId); // Nu c ngi dng ng nhp, set userId
                        localStorage.setItem('currentUser', data.userId); // Lu userId vo localStorage
                    } else {
                        setUserId(null); // Nu khng c ai ng nhp
                    }
                } catch (error) {
                    console.error("Failed to check user login status", error);
                    setUserId(null);
                }
            };

            checkUserLogin(); // Gi hm kim tra khi component mount
        }
    }, []);

    const handleWishlistClick = (product) => {
        const currentUserId = localStorage.getItem('currentUser'); // Kim tra userId t localStorage
        if (!currentUserId) {
            alert("Please log in to add to wishlist");
            return;
        }
        toggleFavorite(product); 
    };

    return {
        userId, 
        handleWishlistClick, 
        isFavorite 
    };
}
